/* Header.css */
.header {
  position: relative;
  height: 100vh;
  overflow: hidden;
  background-color: #151B21;
}

.header-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 90%;
  object-fit: cover;
  z-index: 1;
  margin-top: 5%;
  padding-top: 1.5%;
}

@media screen and (max-width: 200px) {
  .header-video {
    margin-top: 20%;
  }
}

@media screen and (max-width: 468px) {
  .header {
    position: relative;
    height: 50vh;
    width: 100%;
    overflow: hidden;
    background-color: #151B21;
  }

  .header-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    object-fit: cover;
    z-index: 1;
    margin-top: 3%;
    padding-top: 18%;
  }
}

@media (min-width: 668px) and (max-width: 768px) {
  .header {
    position: relative;
    height: 80vh;
    width: 100%;
    overflow: hidden;
    background-color: #151B21;
  }

  .header-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    object-fit: cover;
    z-index: 1;
    margin-top: 2%;
    padding-top: 8%;
  }
}

/* For screens from 768px to 1023px (tablets and small laptops) */
@media (min-width: 768px) and (max-width: 1023px) {
  /* Your responsive styles for tablets and small laptops here */
  .header-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    object-fit: cover;
    z-index: 1;
    margin-top: 0;
    padding-top: 9%;
  }
  
}

