.header-content {
  position: relative;
  z-index: 1;
  text-align: center;
  padding: 100px 0;
  color: #fff;
}

.navbar {
  position: fixed; /* Changed to fixed */
  top: 0;
  left: 0;
  width: 100%;
  background-color: #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0;
  z-index: 1;
}

.nav-logo img {
  height: 50px; /* Adjust the height as needed */
  width: auto; /* Maintain aspect ratio */
}

.nav-item {
  margin: 0 20px;
  cursor: pointer;
  color: #fff;
  font-size: 18px;
  transition: color 0.3s;
}



.dropdown {
  position: relative;
}

.dropdown-content {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #333;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1;
}


.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content a {
  color: #fff;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  transition: background-color 0.3s;
}

.dropdown-content a:hover {
  background-color: #444;
  color: #1e9cd9;

}

