/* Styles for the arrow */
.nav-arrow {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 12px solid #ffffff; /* Arrow color */
  bottom: 25%; /* Position the arrow below the nav item */
  left: 50%;
  transform: translateX(230%);
  z-index: -1;
}


