.contact {
  padding: 100px 0;
  background-color: #151B21;
  color: #f9f9f9;
  text-align: center;
}

.contact-form {
  max-width: 500px;
  margin: 0 auto;
  padding: 50px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}

.contact-form h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px;
  padding-right: 0;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
  color: #333;
}

.contact-form textarea {
  resize: vertical;
  min-height: 100px;
}

.contact-form button {
  width: 100%;
  padding: 10px;
  background-color: #1E9CD9;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.contact-form button:hover {
  background-color: #147AA2;
}

@media screen and (max-width: 768px) {
  .contact-form {
    padding: 30px;
  }
}
