/* Media Query for Mobile */
@media screen and (max-width: 768px) {
  .nav-list {
    display: none;
    position: absolute;
    top: 65px;
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 2;
    text-align: center;
    flex-direction: column;
  }

  .nav-list.open {
    display: flex;
    padding-bottom: 10px;
  }

  .nav-item {
    margin: 10px 0;
  }

  .nav-icon {
    display: block;
    cursor: pointer;
    margin-right: 20px;
  }

  .nav-icon img {
    display: block;
    width: 30px;
    height: 30px;
    margin-right: 30px;
  }
}

.nav-item.active {
  color: #1e9cd9;
}


