.filming-page {
    background-color: #f9f9f9;
    /* margin-top: 3%;
    padding-top: 18%; */
  }
  
  .page-header {
    text-align: center;
    position: relative;
  }
  
  .rectangle {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50vh; /* Adjust the height of the rectangular header */
    background-color: #151B21; /* Color of the rectangular header */
  }
  
  .page-header h1 {
    font-size: 36px;
    color: #f9f9f9;
    margin-top: 10px;
  }
  
  .page-header p {
    font-size: 18px;
    color: #f9f9f9;
    margin-top: 10px;
  }
  
 /* ... (Previous CSS rules) */

.video-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .video-row {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }
  
  .video-container {
    margin: 10px;
    max-width: 400px;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    /* width: 50%; Make each video container take 50% of the width */
  }
  
  .video-container video {
    width: 100%;
    height: auto;
  }
  
  @media screen and (max-width: 768px) {
    .video-container {
      max-width: 100%;
    }

    .video-row {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 5px;
        flex-direction: column-reverse;
    }
  }
  