.your-growth {
    background-color: #151B21;
    padding: 60px 0;
    text-align: center;
  }
  
  .your-growth h2 {
    font-size: 32px;
    margin-bottom: 20px;
  }
  
  .growth-boxes {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;
    margin: 30px;
  }
  
  .growth-box {
    background-color: #2C343A;
    border: 1px solid #333;
    border-radius: 10px;
    padding: 20px;
    margin: 0 10px;
    text-align: left;
    width: calc(33.33% - 20px); /* Divide width equally among three boxes */
  }
  
  .growth-box h1 {
    font-size:28px;
    margin-bottom: 10px;
    color: #FFDF00;
  }
  
  .growth-box p {
    font-size: 19px;
    color: #f9f9f9;
  }
  
  .growth-box:hover {
    background-color: #1A9CD9;
    .growth-box h1 {
        color: #f9f9f9;
      }
}

.your-color {
    color: #1A9CD9; /* Text color for "Your" */
  }
  
  .growth-color {
    color: #FFDF00; /* Text color for "Growth" */
  }

  span h4 {
    padding-left: 10px;
  }

  /* Media queries for responsiveness */
@media (max-width: 768px) {
  .your-growth {
    overflow: hidden;
  }
  .growth-box {
    width: calc(70% - 20px); /* Divide width equally among two boxes */
    margin: 15px;
    padding: 30px;
    max-width: 100%;
  }

  .growth-boxes {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .growth-box h1 {
    font-size:20px;
    margin-bottom: 10px;
    color: #FFDF00;
  }
  
  .growth-box p {
    font-size: 14px;
    color: #f9f9f9;
  }
}