/* AboutUs.css */

.about-us {
    background-color: #151B21;
    padding: 40px;
  }

/* Center the AboutUs content vertically and horizontally */
.about-us {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  /* Style the video container */
  .video-container {
    margin-top: 20px; /* Add spacing above the video */
  }
  
  
  .about-content {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
    padding-top: 50px;
  }
  
  .about-content h1 {
    font-size: 35px;
    color: honeydew;
  }

  .about-us h2 {
    font-size: 35px;
    color: honeydew;
  }
  
  .about-content p {
    font-size: 25px;
    color: white;
    margin-bottom: 20px;
    background: #2C343A;
    padding: 20px;
    border-radius: 8px;
  }
  