/* Clients.css */
.clients {
    padding: 20px;
    background-color: #151B21;
  }
  
  .clients h2 {
    font-size: 24px;
    margin-bottom: 20px;
    color: #f9f9f9;
  }
  
  .client-logos {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .client-logos img {
    max-width: 100%;
    height: auto;
    max-height: 100px;
  }
  @media screen and (max-width: 768px) {
    .clients {
      position: relative;
      overflow: hidden;
    }
    .client-logos img {
      width: calc(33% - 20px); /* Divide width equally among two logos */
    }
  }